.front_container{
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* 제목 */
.front_title_content{
    width: 100%;
    height: 5%;
    line-height: normal;
    margin: auto;
    background-color: #3578FF;
    color: white;
    font-size: 16px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* 카테고리 */
.front_category_container{
    height: 4%;
    line-height: normal;
    background-color: #3578FF;
    display: flex;
    justify-content: center;
    align-items: center;
}
.front2_category_content{
    width: 80%;
    height: 70%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
}
.front_category{
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    font-size: 8px;
}


/* 퀴즈 제목 */
.front_quiz_title_container{
    width: 80%;
    height: 3%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: 16px;
    font-weight: bold;
}


/* 퀴즈 정보 */
.front2_quiz_info_container{
    width: 100%;
    /* height: 75%; */
    height: 60%;
}


.front_quiz_info_question_title_content{
    height: 4%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
    color: #3578FF;
}


.front2_quiz_info_question_container{
    width: 90%;
    height: 20%;
    margin: auto;
}
.front_quiz_info_question{
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    word-wrap: break-word; 
    overflow-wrap: break-word;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
}



.front2_quiz_info_line_content{
    width: 90%;
    height: 1%;
    margin: auto;
    text-align: center;
}
.front_quiz_info_line{
    height: 0.5px;
    background-color: #3578FF;
}


.front_quiz_info_answer_title_content{
    height: 4%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
    color: #3578FF;
}


.front2_quiz_info_answer_container{
    width: 90%;
    height: 71%;
    margin: auto;
}
.front2_quiz_info_answer{
    height: 100%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    word-wrap: break-word; 
    overflow-wrap: break-word;
    text-align: center;
    font-size: 11px;
    font-weight: bold;
}

/* 퀴즈 선택 */
.front_quiz_select_container{
    width: 85%;
    height: 4%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
    color: #3578FF;
    font-size: 14px;
    font-weight: bold;
}
.front_quiz_select_content{
    border: 1.5px solid #3578FF;
    padding: 3px;
    cursor: pointer;
}